/*jshint esversion: 6 */

function createImgPromise(url) {
    return new Promise((resolve, reject) => {
        let img = new Image();
        img.src = url;
        img.onload = () => {
            resolve(img);
        };
    });
}

export function drawObj(container, status, item) {
    let canvas = document.createElement('canvas');
    canvas.width = 550;
    canvas.height = 550;
    container.appendChild(canvas);
    let ctx = canvas.getContext('2d');

    let wall, floor, target, sheep, wolf, sheepBeCatch;
    let wallImageUrl = './static/img/wall.png';
    let floorImageUrl = './static/img/floor.png';
    let targetImageUrl = './static/img/target.png';

    let sheepImageUrl = './static/img/sheep.png';
    let wolfImageUrl = './static/img/wolf.png';
    let sheepBeCatchImageUrl = './static/img/sheep-be-catch.png';

    let wallImage = createImgPromise(wallImageUrl);
    let floorImage = createImgPromise(floorImageUrl);
    let targetImage = createImgPromise(targetImageUrl);
    let sheepImage = createImgPromise(sheepImageUrl);
    let wolfImage = createImgPromise(wolfImageUrl);
    let sheepBeCatchImage = createImgPromise(sheepBeCatchImageUrl);
    function drawImage(image, x, y) {
        ctx.drawImage(image, item.base * x, item.base * y, item.base, item.base);
    }
    Promise.all([wallImage, floorImage, targetImage, sheepImage, wolfImage, sheepBeCatchImage]).then((data) => {
        wall = data[0];
        floor = data[1];
        target = data[2];
        sheep = data[3];
        wolf = data[4];
        sheepBeCatch = data[5];
        status.forEach(function (rowArr, y) {
            rowArr.forEach(function (num, x) {
                switch(num) {
                    case 0:
                        //默认背景
                        ctx.fillStyle = 'transparent';
                        ctx.fillRect(item.base * x, item.base * y, item.base, item.base);
                        break;
                    case 1:
                        //墙
                        drawImage(wall, x, y);
                        console.log('墙壁');
                        break;
                    case 2:
                        //地板
                        console.log('地板');
                        drawImage(floor, x, y);
                        break;
                    case 3:
                        //羊
                        drawImage(floor, x, y);
                        drawImage(sheep, x, y);
                        break;
                    case 4:
                        //终点
                        drawImage(floor, x, y);
                        drawImage(target, x, y);
                        break;
                    case 5:
                        //狼
                        drawImage(floor, x, y);
                        drawImage(wolf, x, y);
                        break;

                }
            });
        });
    });
}